<template>
  <div class="address">
    <div class="address-top">
      <van-icon @click="fh" size="22" name="arrow-left" />
      <p>新增地址</p>
    </div>
    <ul class="address-ul">
      <li>
        <div>
          <label for>联系人</label>
          <input type="text" placeholder="姓名" v-model="linkMan" />
        </div>
        <p></p>
      </li>
      <li>
        <div>
          <label for>手机号码</label>
          <input type="text" placeholder="11位手机号" v-model="mobile" />
        </div>
        <p></p>
      </li>
      <li>
        <div @click="xzdq">
          <label for>选择地区</label>
          <div class="div11" v-if="list.length>0">
            <p>{{list[0].name}}</p>
            <p>{{list[1].name}}</p>
            <p>{{list[2].name}}</p>
          </div>
        </div>
        <p></p>
      </li>
      <li>
        <div>
          <label for>详细地址</label>
          <input type="text" placeholder="街道门牌信息" v-model="address" />
        </div>
        <p></p>
      </li>
      <li>
        <div>
          <label for>邮政编码</label>
          <input type="text" placeholder="邮政编码" v-model="code" />
        </div>
        <p></p>
      </li>
    </ul>
    <van-button type="primary" @click="baocun" block>保存</van-button>

    <van-popup v-model="show" position="bottom">
      <van-area title="标题" :area-list="areaList" @confirm="ok" @cancel="cancel" />
    </van-popup>
  </div>
</template>

<script>
import areaList from "../area";
import qs from "qs"
export default {
  name: "Address",
  data() {
    return {
      show: false,
      areaList: areaList,
      list: [], //城市列表
      linkMan:"", //姓名
      mobile:"",  //手机号
      address:"" , //详细地址
      code:"",   //邮编
    };
  },
  methods: {
    xzdq() {
      this.show = !this.show;
    },
    cancel() {
      this.show = false;
    },
    ok(data) {
      console.log(data);
      this.list = data;
      this.show=false
    },
    baocun(){
        var arr ={
            address:this.address,
            cityId:this.list[1].code,
            code:this.code,
            linkMan:this.linkMan,
            mobile:this.mobile,
            provinceId:this.list[0].code,
            token:localStorage.getItem("yx-token")
        }
        var data=qs.stringify(arr)
        this.$axios.post("/user/shipping-address/add",data).then((res)=>{
            console.log(res)
        })
    },
    fh(){
        this.$router.go(-1)
    }
  }
};
</script>

<style lang="scss" scoped>
.address {
  width: 100%;
  height: 100%;
  position: relative;
  .address-top {
    position: fixed;
    top: 0;
    width: 100%;
    height: 0.74rem;
    display: flex;
    align-items: center;
    .van-icon {
      text-indent: 0.1rem;
    }
    p:nth-of-type(1) {
      margin-left: 2.2rem;
    }
  }
  .address-ul {
    width: 100%;
    margin-top: 0.74rem;
    padding: 0.7rem 0.2rem 0 0.2rem;
    box-sizing: border-box;
    li {
      height: 0.81rem;
      div {
        border-bottom: 0.01rem solid #dddddd;
        height: 0.8rem;
        margin-left: 0.04rem;
        display: flex;
        align-items: center;
        label {
          font-size: 0.26rem;
          width: 1.1rem;
        }
        input {
          font-size: 0.24rem;
          width: 4.4rem;
          border: none;
          margin-left: 0.4rem;
          background: #ffffff;
          height: 0.64rem;
        }
      }
    }
  }
}
.div11{
    margin-left: .4rem;
    p{
        margin-left: .4rem
    }
}
</style>


