<template>
  <div class="diandan">
    <div class="top">
      <van-icon @click="fh" size="22" name="arrow-left" />
      <p>确认订单</p>
      <p></p>
    </div>
    <div class="dingdan-box">
      <div class="nav1">
        <ul>
          <li>
            <van-icon name="location" @click="xuanzedizhi" />
            <div class="count">
              <p>
                <span>{{add.linkMan}}</span>
                <span>{{add.mobile}}</span>
              </p>
              <p>
                <span>{{add.provinceStr}}</span>
                <span>{{add.address}}</span>
              </p>
            </div>
            <van-icon name="share" />
          </li>
        </ul>
      </div>
      <!-- <div v-else @click="xianjian" class="dizhi">
        <p>+</p>
        <div>新增收货地址</div>
        <van-icon name="arrow" />
        <p class="p2"></p>
      </div>-->

      <p class="p1">商品列表</p>
      <ul class="ul1">
        <li v-for="(itme,key) in this.$store.getters.list">
          <img :src="itme.img" alt />
          <div class="count">
            <p v-html="itme.name"></p>
            <p>
              <span>{{itme.tit1}}</span>
              <span>{{itme.name1}}</span>
            </p>
          </div>
          <div class="rigth">
            <p>￥{{itme.price}}</p>
            <p>x{{itme.num}}</p>
          </div>
        </li>
      </ul>
      <div class="nav">
        <p>配送方式</p>
        <p>快递</p>
      </div>
      <div class="bei">
        <label for>备注</label>
        <input @focus="huoqu" type="text" placeholder="如需备注请输入" />
      </div>
      <p id="bott"></p>
      <div class="price">
        <p>商品金额</p>
        <p>￥{{this.$store.getters.num}}</p>
      </div>
      <div class="botton">
        <p>合计:￥{{this.$store.getters.num}}</p>
        <van-button type="danger" @click="tijiao">提交订单</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Dingdan",
  data() {
    return {
      list: JSON.parse(localStorage.getItem("yx-dingdan")) || [],
      add: [],
      list1:[]
    };
  },
  mounted() {
    // console.log(localStorage.getItem("yx-token"));
    //   var token=localStorage.getItem("yx-token")
    axios
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("yx-token")
      )
      .then(res => {
        console.log(res.data.data);
        this.add = res.data.data;
      });
  },
  methods: {
    huoqu() {
      console.log("获取焦点");
      var p = document.getElementById("bott");
      p.style.width = "100%";
    },
    xianjian() {
      this.$router.push("/addlist");
    },
    fh() {
      this.$router.go(-1);
    },
    xuanzedizhi() {
      this.$router.push("/addlist");
    },
    tijiao() {
      var number = "1234567891011121314151617181920212223";
      var arr = "";
      function rnd(n, m) {
        return parseInt(Math.random() * (m - n) + n);
      }
      for (var i = 0; i < 10; i++) {
        var n = parseInt(Math.random() * arr.length);
        arr += number[n];
      }
      
      for(var i=0;i<this.list.length;i++){
        this.$set(this.list[i],"dingdanhao",arr)
        
      }
      localStorage.setItem("ys-Confirm order", JSON.stringify(this.list))
      this.$router.push("/Confirmorder")
    }
  }
};
</script>

<style lang="scss" scoped>
.dingdan-box {
  margin-top: 0.8rem;
  .p1 {
    width: 100%;
    height: 0.8rem;
    border-top: 0.1rem solid #f5f5f5;
    border-bottom: 0.1rem solid #f5f5f5;
    line-height: 0.8rem;
    text-indent: 0.2rem;
  }
  .p2::before {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 0.02rem;
    top: 1.09rem;
    background: -webkit-repeating-linear-gradient(
      135deg,
      #ff6c6c 0,
      #ff6c6c 20%,
      transparent 0,
      transparent 25%,
      #1989fa 0,
      #1989fa 45%,
      transparent 0,
      transparent 50%
    );
    background: repeating-linear-gradient(
      -45deg,
      #ff6c6c 0,
      #ff6c6c 20%,
      transparent 0,
      transparent 25%,
      #1989fa 0,
      #1989fa 45%,
      transparent 0,
      transparent 50%
    );
    background-size: 60px;
    content: "";
  }
  .dizhi {
    width: 100%;
    display: flex;
    height: 1.1rem;
    padding: 0 0.2rem;
    align-items: center;
    box-sizing: border-box;
    position: relative;
    p:nth-of-type(1) {
      width: 0.6rem;
      height: 0.6rem;
      background: #0098e9;
      border-radius: 0.06rem;
      text-align: center;
      font-size: 0.5rem;
      color: ghostwhite;
      margin-left: 0.3rem;
    }
    div:nth-of-type(1) {
      width: 4.5rem;
      height: 0.58rem;
      margin-left: 0.32rem;
      line-height: 0.58rem;
    }
    .van-icon {
      color: gainsboro;
    }
  }
}
.ul1 {
  width: 100%;
  border-bottom: 0.15rem solid #f5f5f5;
  li {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // padding: 0.2rem;
    height: 1.5rem;
    border-bottom: 1px solid #e8e8e8;
    box-sizing: border-box;
    img {
      width: 1.2rem;
      height: 1.2rem;
      display: block;
      margin-left: 0.2rem;
      //   margin: 0 auto;
    }
    .count {
      margin-left: 0.2rem;
      width: 3rem;
      box-sizing: border-box;
      height: 100%;
      p:nth-of-type(1) {
        margin-top: 0.2rem;
      }
      p:nth-of-type(2) {
        margin-top: 0.4rem;
        color: gainsboro;
        font-size: 0.2rem;
      }
    }
    .rigth {
      width: 1rem;
      height: 100%;
      p:nth-of-type(1) {
        margin-top: 0.2rem;
      }
      p:nth-of-type(2) {
        margin-top: 0.4rem;
        color: gainsboro;
      }
    }
  }
}
.nav {
  width: 100%;
  height: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid gainsboro;
  p:nth-of-type(1) {
    text-indent: 0.4rem;
  }
  p:nth-of-type(2) {
    margin-right: 0.3rem;
  }
}
.bei {
  width: 100%;
  height: 0.8rem;
  padding: 0 0.2rem;
  display: flex;
  align-items: center;
  input {
    width: 4rem;
    height: 0.4rem;
    border: none;
    margin-left: 0.7rem;
    color: black;
  }
}
#bott {
  width: 0%;
  height: 0.01rem;
  background: #0098e9;
  -webkit-transition: all 1s;
  transition: all 1s;
  margin: 0 auto;
}
.price {
  width: 100%;
  padding: 0 0.2rem;
  height: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 0.15rem solid #f5f5f5;
  border-bottom: 0.15rem solid #f5f5f5;
  box-sizing: border-box;
}
.botton {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  bottom: 0;
  p:nth-of-type(1) {
    text-indent: 2.2rem;
    color: red;
  }
}
.diandan {
  width: 100%;
  position: relative;
  .top {
    width: 100%;
    height: 0.8rem;
    position: fixed;
    top: 0;
    left: 0;
    background: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.3rem;
    box-sizing: border-box;
  }
}
.nav1 {
  ul {
    width: 100%;
    li {
      width: 6.23rem;
      height: 1.4rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-left: 0.3rem;
      box-sizing: border-box;
      border-bottom: 1px solid #f5f5f5;
      .count {
        width: 4rem;
        height: 1rem;
        p:nth-of-type(1) {
          margin-top: 0.1rem;
          span:nth-of-type(2) {
            margin-left: 0.3rem;
          }
        }
        p:nth-of-type(2) {
          margin-top: 0.3rem;
          color: gainsboro;
          font-size: 0.2rem;
          span:nth-of-type(2) {
            margin-left: 0.3rem;
          }
        }
      }
    }
  }
}
</style>


