import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)
let store = new Vuex.Store({
    state: {
        list: JSON.parse(localStorage.getItem("ys-list")) || [],

    },
    mutations: {
        add(state, obj) {
            let cart = localStorage.getItem("ys-list")
            let index;
            if (cart != null) {
                index = state.list.findIndex((itme, key) => {
                    return itme.id == obj.id && itme.tit1 == obj.tit1 && itme.name == obj.name
                })
            }
            if (index > -1) {
                state.list[index].num += obj.num
            } else {
                state.list.unshift(obj)
            }
            localStorage.setItem("ys-list", JSON.stringify(state.list))
            //    console.log(obj)
            //    console.log(state.list)
        },
        dianji(state, k) {
            state.list[k].check = !state.list[k].check
            localStorage.setItem("ys-list", JSON.stringify(state.list))
        },
        del(state) {
            let flag = false
            for (var i = 0; i < state.list.length; flag ? i : i++) {
                if (state.list[i].check == true) {
                    state.list.splice(i, 1)
                    flag = true
                } else {
                    flag = false
                }

            }
            //    state.list=state.list.filter((itme)=>{
            //         return itme.check==true
            //     })
            localStorage.setItem("ys-list", JSON.stringify(state.list))
        },
        jia(state, k) {
            console.log(k)
            state.list[k].num++
            localStorage.setItem("ys-list", JSON.stringify(state.list))
        },
        jian(state, k) {
            console.log(k)
            state.list[k].num--

        },
        remove(state) {
           
            // localStorage.setItem("ys-list", JSON.stringify(state.list))
        }
    },
    getters: {
        number(state) {
            let num = 0
            state.list.forEach((itme) => {
                num += itme.num
            })
            return num
        },
        num(state) {
            let num = 0
            state.list.forEach((itme) => {
                num += itme.num * itme.price
            })
            return num
        },
        list(state) {
            return state.list.filter((itme) => {
                return itme.check == true
            })
        }
    }
})
export default store


