<template>
  <!-- 购物车页面 -->
  <div class="goods">
    <!-- 顶部位置 -->
    <div class="goods-top">
      <p @click="bj">
        <span v-show="show">编辑</span>
        <span v-show="!show">完成</span>
      </p>
      <p>购物车</p>
      <p></p>
    </div>
    <!-- 购物车内容 -->
    <ul id="ul1">
      <li id="li1" v-for="(itme,key) of list">
        <div @click="dianji(key)" class="left">
          <van-checkbox v-model="itme.check"></van-checkbox>
        </div>
        <img :src="itme.img" alt />
        <div class="goods-left">
          <p v-html="itme.name"></p>
          <p></p>
          <p>规格:{{itme.tit1}} &nbsp{{itme.name1}}</p>
          <p>￥{{itme.price}}</p>
        </div>
        <p>
          <van-stepper v-model="itme.num" @minus="jian(key)" @plus="jia(key)" />
        </p>
      </li>
    </ul>
    <!-- 猜你喜欢 -->
    <div class="nav">
      <div class="zt-top">
        <p>猜你喜欢</p>
      </div>
      <ul id="ul-ul">
        <li id="ul-li" @click="add(itme.id)" v-for="(itme,key) in list1" :key="itme.id">
          <img :src="itme.pic" alt />
          <p v-html="itme.name"></p>
          <p v-html="itme.characteristic"></p>
          <div>
            <span>￥{{itme.minPrice}}</span>
            <span>已售{{itme.numberSells}}</span>
          </div>
          <div v-html="itme.name"></div>
        </li>
      </ul>
    </div>
    <!-- 底部删除和下单 -->
    <div class="goods-bottom">
      <div class="goods-div" v-show="!show">
        <div @click="qx" class="checkbox">
          <van-checkbox v-model="flag">全选</van-checkbox>
        </div>
        <p>合计:￥{{this.$store.getters.num}}</p>
        <van-button @click="del" type="danger">删除</van-button>
      </div>
      <div class="goods-div" v-show="show">
        <div @click="qx" class="checkbox">
          <van-checkbox v-model="flag">全选</van-checkbox>
        </div>
        <p>合计:￥{{this.$store.getters.num}}</p>
        <van-button type="danger" @click="xiadan">下单</van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: this.$store.state.list,
      show: true,
      flag: false,
      list1:[]
    };
  },
  mounted(){
    this.getxh()
  },
  methods: {
    bj() {
      this.show = !this.show;
    },
    dianji(k) {
      console.log(this.list[k].check);
      //   this.list[k].check=!this.list[k].check
      this.$store.commit("dianji", k);
      this.flag = this.list.every(itme => {
        return itme.check == true;
      });
    },
    qx() {
      this.flag = !this.flag;
      this.list.forEach(itme => {
        itme.check = this.flag;
      });
    },
    del() {
      this.$store.commit("del");
    },
    jia(k) {
      this.$store.commit("jia", k);
    },
    jian(k) {
      this.$store.commit("jian", k);
    },
    xiadan() {
      this.list.forEach(itme => {
        if (itme.check == true) {
          localStorage.setItem("yx-dingdan", JSON.stringify(this.list));
        }
      });
      this.$router.push("/dingdan");
    },
    getxh() {
      this.$axios("https://api.it120.cc/small4/shop/goods/list").then(res => {
        var arr = res;
        function getRandom(n) {
          var newarr = [],
          leng = arr.length;
          for (var i = 0; i < n; i++) {
            var str = Math.floor(Math.random() * leng);
            newarr.push(arr[str]);
          }
          console.log(leng);
          return newarr;
        }
        var aa = getRandom(4);
        this.list1 = aa;

        
      });
    },
    add(id){
      this.$router.push({name: "Details",params: {id: id }});
    }
  }
};
</script>

<style lang="scss" scoped>
.goods {
  position: relative;
  width: 100%;
  margin-bottom: 1.5rem;
  #ul1 {
    margin-top: 0.6rem;
    width: 100%;
    #li1 {
      border-bottom: 1px solid gainsboro;
      padding: 0.3rem;
      display: flex;
      img {
        width: 1.5rem;
        margin-left: 0.3rem;
      }
      .goods-left {
        width: 40%;
        box-sizing: border-box;
        // border: 1px solid red;
        // margin-left: 0.2rem;
        p:nth-of-type(1) {
          margin-top: 0.2rem;
          text-indent: 0.3rem;
          font-size: 0.2rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p:nth-of-type(2) {
          margin-top: 0.2rem;
          text-indent: 0.3rem;
        }
        p:nth-of-type(3) {
          margin-top: 0.1rem;
          color: gainsboro;
          font-size: 0.18rem;
          text-indent: 0.3rem;
        }
        p:nth-of-type(4) {
          margin-top: 0.1rem;
          color: gainsboro;
          font-size: 0.18rem;
          text-indent: 0.3rem;
          color: red;
        }
      }
      p:nth-of-type(1) {
        margin-top: 1rem;
        // margin-right: ;
        width: 2rem;
      }
    }
  }
}
.goods-top {
  width: 100%;
  height: 0.6rem;
  border-bottom: 1px solid gainsboro;
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  p:nth-of-type(1) {
    color: red;
  }
}
.goods-bottom {
  width: 100%;
  // height: 0.7rem;
  border-bottom: 1px solid gainsboro;
  border-top: 1px solid gainsboro;
  position: fixed;
  bottom: .78rem;
  background: white;
  .goods-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      color: red;
    }
  }
}

.left {
  width: 0.5rem;
  margin-top: 0.8rem;
}
//猜你喜欢
.zt-top {
  width: 100%;
  height: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
#ul-ul {
  width: 100%;
  // padding: 1%;
  box-sizing: border-box;
  display: flex;
  // padding: 0 0.2rem;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-top: 0.2rem;
  border-bottom: 0.14rem solid #f5f5f5;
  #ul-li {
    width: 48%;
    position: relative;
    img {
      width: 100%;
      height: 3.5rem;
      display: block;
    }
    p:nth-of-type(1) {
      font-size: 0.2rem;
      color: #000000;
      width: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    p:nth-of-type(2) {
      font-size: 0.14rem;
      color: #b2b2b2;
      width: 2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: 0.1rem;
    }
    div:nth-of-type(1) {
      display: flex;
      // width: 98%;
      justify-content: space-between;
      align-items: center;
      padding: 1%;
      font-size: .26rem;
      span:nth-of-type(1) {
        font-size: 0.26rem;
        color: #d00000;
        // margin-top: 0.1rem;
      }
      span:nth-of-type(2) {
        font-size: 0.2rem;
        color: gainsboro;
        // margin-top: 0.1rem;
      }
    }
    div:nth-of-type(2){
      width: 98%;
      position: absolute;
      top: 3.1rem;
      background: #f0edd4;
      color: #b69b72;
      padding: 1%;
      text-indent: .2rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>


