<template>
  <div class="addlist">
    <div class="addlist-top">
      <van-icon @click="fh" size="22" name="arrow-left" />
      <p>我的地址</p>
      <p @click="xianjian">新建地址</p>
    </div>
    <div class="nav">
        <ul>
            <li v-for="(itme,key) of list" >
                <van-checkbox @click="xuanze(key)" v-model="itme.isDefault"></van-checkbox>
                <div class="count">
                    <p><span>{{itme.linkMan}}</span> <span>{{itme.mobile}}</span></p>
                    <p><span>{{itme.provinceStr}}</span><span>{{itme.address}}</span></p>
                </div>
                <van-icon name="share" />
            </li>
        </ul>
    </div>
  </div>
</template>

<script>
import qs from "qs"
export default {
  name: "Addlist",
  data () {
      return {
          list:[],
          checked:false
      }
  },
  methods:{
      fh(){
          this.$router.go(-1)
      },
      xuanze(k){
          this.list[k].isDefault=true
          localStorage.setItem("ys-list1", JSON.stringify(this.list))
      },
      xianjian(){
          this.$router.push("/address")
      }
  },
  mounted(){
      var d ={"token":localStorage.getItem("yx-token")}
      var data=qs.stringify(d)
      this.$axios.post("/user/shipping-address/list",data).then((res)=>{
          console.log(res)
          this.list=res
      })
  }
};
</script>

<style lang="scss" scoped>
.addlist {
  width: 100%;
  height: 100%;
  .addlist-top {
    position: fixed;
    top: 0;
    width: 100%;
    height: 0.74rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    .van-icon {
      text-indent: 0.1rem;
    }
    p:nth-of-type(2){
        margin-right: .3rem
    }
  }
}
.nav{
    margin-top: .74rem;
    ul{
        width: 100%;
        li{
            width: 6.23rem;
            height: 1.4rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: .3rem;
            box-sizing: border-box;
            border-bottom: 1px solid #f5f5f5;
            .count{
                width: 4rem;
                height: 1rem;
                p:nth-of-type(1){
                    margin-top: .1rem;
                    span:nth-of-type(2){
                        margin-left: .3rem
                    }
                }
                p:nth-of-type(2){
                    margin-top: .3rem;
                    color: gainsboro;
                    font-size: .2rem;
                    span:nth-of-type(2){
                        margin-left: .3rem
                    }
                }
            }
        }
    }
}
</style>


