<template>
  <div class="Confirmorder">
    <div class="Confirmorder-top">
      <van-icon @click="fh" size="22" name="arrow-left" />
      <p>确认订单</p>
      <p></p>
    </div>
    <ul class="Confirmorder-ul">
      <li v-for="itme in list">
        <van-icon name="after-sale" />
        <div>
          <p>商品总额:{{itme.price}}</p>
          <p>订单号:￥{{itme.dingdanhao}}</p>
        </div>
      </li>
    </ul>
    <div class="nav1">
      <ul>
        <li>
          <van-icon name="location" />
          <div class="count">
            <p>
              <span>{{add.linkMan}}</span>
              <span>{{add.mobile}}</span>
            </p>
            <p>
              <span>{{add.provinceStr}}</span>
              <span>{{add.address}}</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div class="botton" @click="remove">微信支付</div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Confirmorder",
  data() {
    return {
      list: JSON.parse(localStorage.getItem("ys-Confirm order")) || [],
      add: []
    };
  },
  mounted() {
    document.title = "支付";
    axios
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("yx-token")
      )
      .then(res => {
        console.log(res.data.data);
        this.add = res.data.data;
      });
  },
  methods: {
    remove() {
      this.$toast.success("支付成功");
      localStorage.removeItem("ys-Confirm order");
      localStorage.removeItem("yx-dingdan");
      localStorage.removeItem("ys-list");
      this.$store.commit("remove");
      this.$router.push("/home/my");
    },
    fh() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.Confirmorder {
  position: relative;
  width: 100%;
  height: 100%;
  .Confirmorder-top {
    width: 100%;
    height: 0.8rem;
    border-bottom: 1px solid gainsboro;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .van-icon {
      text-indent: 0.1rem;
    }
  }
  .Confirmorder-ul {
    width: 100%;
    li {
      width: 100%;
      display: flex;
      // justify-content: space-between;
      align-items: center;
      padding: 0.2rem;
      border-bottom: 1px solid gainsboro;
      .van-icon {
        width: 1rem;
        font-size: 0.42rem;
        text-align: center;
        color: red;
      }
      div {
        margin-left: 0.3rem;
        width: 4rem;
        p:nth-of-type(2) {
          margin-top: 0.3rem;
          color: gainsboro;
          font-size: 0.2rem;
        }
      }
    }
  }
  .nav1 {
    ul {
      width: 100%;
      li {
        width: 6.23rem;
        height: 1.4rem;
        display: flex;
        align-items: center;
        //   justify-content: space-between;
        padding-left: 0.3rem;
        box-sizing: border-box;
        border-bottom: 1px solid #f5f5f5;
        .count {
          width: 4rem;
          height: 1rem;
          p:nth-of-type(1) {
            margin-top: 0.1rem;
            span:nth-of-type(2) {
              margin-left: 0.3rem;
            }
          }
          p:nth-of-type(2) {
            margin-top: 0.3rem;
            color: gainsboro;
            font-size: 0.2rem;
            span:nth-of-type(2) {
              margin-left: 0.3rem;
            }
          }
        }
        .van-icon {
          width: 1rem;
          text-align: center;
          color: #5a9fec;
          font-size: 0.45rem;
        }
      }
    }
  }
  .botton {
    width: 100%;
    height: 0.8rem;
    position: fixed;
    bottom: 0;
    text-align: center;
    line-height: 0.8rem;
    background: red;
  }
}
</style>


